import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { MatButtonModule } from '@angular/material/button';
import { MatChipsModule } from '@angular/material/chips';
import { MatRippleModule } from '@angular/material/core';
import { MatExpansionModule } from '@angular/material/expansion';
import { MatFormFieldModule } from '@angular/material/form-field';
import { MatIconModule } from '@angular/material/icon';
import { MatInputModule } from '@angular/material/input';
import { MatPaginatorModule } from '@angular/material/paginator';
import { MatSelectModule } from '@angular/material/select';
import { MatSnackBarModule } from '@angular/material/snack-bar';
import { MatSortModule } from '@angular/material/sort';
import { MatTableModule } from '@angular/material/table';
import { MatTabsModule } from '@angular/material/tabs';
import { MatProgressSpinnerModule } from '@angular/material/progress-spinner';

import { MatDialogModule } from '@angular/material/dialog';
import { NgxChartsModule } from '@swimlane/ngx-charts';
import { AgmCoreModule } from '@agm/core';

import { FuseSharedModule } from '@fuse/shared.module';
import { FuseWidgetModule } from '@fuse/components/widget/widget.module';

import { AlmacenProductosComponent,ProductosTiendaDialog} from 'app/main/apps/almacen/productos/productos.component';
import { AlmacenProductosService } from 'app/main/apps/almacen/productos/productos.service';
import { AlmacenProductoComponent } from 'app/main/apps/almacen/producto/producto.component';
import { AlmacenProductoService } from 'app/main/apps/almacen/producto/producto.service';
import { AlmacenOrdenesComponent } from 'app/main/apps/almacen/ordenes/ordenes.component';
import { AlmacenOrdenesService } from 'app/main/apps/almacen/ordenes/ordenes.service';
import { AlmacenOrdenComponent } from 'app/main/apps/almacen/orden/orden.component';
import { AlmacenOrdenService } from 'app/main/apps/almacen/orden/orden.service';

import { JmyModule } from 'app/concomsis/jmy/jmy.module';


const routes: Routes = [
    {
        path     : 'productos',
        component: AlmacenProductosComponent,
        resolve  : {
            data: AlmacenProductosService
        }
    },
    {
        path     : 'productos/:id',
        component: AlmacenProductoComponent,
        resolve  : {
            data: AlmacenProductoService
        }
    },
    {
        path     : 'productos/:id/:handle',
        component: AlmacenProductoComponent,
        resolve  : {
            data: AlmacenProductoService
        }
    },
    {
        path     : 'ordenes',
        component: AlmacenOrdenesComponent,
        resolve  : {
            data: AlmacenOrdenesService
        }
    },
    {
        path     : 'ordenes/:id',
        component: AlmacenOrdenComponent,
        resolve  : {
            data: AlmacenOrdenService
        }
    }
];

@NgModule({
    declarations: [
        AlmacenProductosComponent,
        ProductosTiendaDialog,
        AlmacenProductoComponent,
        AlmacenOrdenesComponent,
        AlmacenOrdenComponent
    ],
    imports     : [
        RouterModule.forChild(routes),

        MatButtonModule,
        MatChipsModule,
        MatExpansionModule,
        MatFormFieldModule,
        MatIconModule,
        MatInputModule,
        MatPaginatorModule,
        MatRippleModule,
        MatSelectModule,
        MatSortModule,
        MatDialogModule,
        MatSnackBarModule,
        MatTableModule,
        MatTabsModule,
        MatProgressSpinnerModule,
        JmyModule,
        NgxChartsModule,
        AgmCoreModule.forRoot({
            apiKey: 'AIzaSyD81ecsCj4yYpcXSLFcYU97PvRsE_X8Bx8'
        }),

        FuseSharedModule,
        FuseWidgetModule
    ],
    providers   : [
        AlmacenProductosService,
        AlmacenProductoService,
        AlmacenOrdenesService,
        AlmacenOrdenService
    ],
    entryComponents:[
        ProductosTiendaDialog
    ]

})
export class AlmacenModule
{
}
