import { Component, OnDestroy, OnInit, ViewEncapsulation } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Location } from '@angular/common';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';

import { fuseAnimations } from '@fuse/animations';
import { FuseUtils } from '@fuse/utils';

import { Product } from 'app/main/apps/almacen/producto/producto.model';
import { AlmacenProductoService } from 'app/main/apps/almacen/producto/producto.service';


@Component({
    selector     : 'almacen-producto',
    templateUrl  : './producto.component.html',
    styleUrls    : ['./producto.component.scss'],
    encapsulation: ViewEncapsulation.None,
    animations   : fuseAnimations
})
export class AlmacenProductoComponent implements OnInit, OnDestroy
{
    product: Product;
    pageType: string;
    productForm: FormGroup;
    configuracionArchivos:any;
    cargandoArchivos:boolean=false;
    // Private
    private _unsubscribeAll: Subject<any>;

    /**
     * Constructor
     *
     * @param {EcommerceProductService} _ecommerceProductService
     * @param {FormBuilder} _formBuilder
     * @param {Location} _location
     * @param {MatSnackBar} _matSnackBar
     */
    constructor(
        private _ecommerceProductService: AlmacenProductoService,
        private _formBuilder: FormBuilder,
        private _location: Location,
        private _matSnackBar: MatSnackBar
    )
    {
        // Set the default
        this.product = new Product();

        // Set the private defaults
        this._unsubscribeAll = new Subject();

        this._ecommerceProductService.jmyService.jmyFn.botonEnEsperaArchivos  .pipe(takeUntil(this._unsubscribeAll)).subscribe(respuesta=>{
            //console.log('botonEnEsperaArchivos o loading',respuesta);
            this.cargandoArchivos=respuesta;
        })


        this._ecommerceProductService.jmyService.jmyFn.configuracionArchivos  .pipe(takeUntil(this._unsubscribeAll)).subscribe(respuesta=>{

            this.configuracionArchivos=respuesta||{};
           // console.log(respuesta);
            
        });
    }

    // -----------------------------------------------------------------------------------------------------
    // @ Lifecycle hooks
    // -----------------------------------------------------------------------------------------------------

    /**
     * On init
     */
    ngOnInit(): void
    {
        // Subscribe to update product on changes
        this._ecommerceProductService.onProductChanged
            .pipe(takeUntil(this._unsubscribeAll))
            .subscribe(product => {

                if ( product )
                {
                    this.product = new Product(product);
                    this.pageType = 'edit';

                    console.log('si existe producto', this.product, this.configuracionArchivos);
                    
                    const archivosQuery = Object.assign(this.configuracionArchivos||{},{
                        modulo:'almacen',
                        raiz:this._ecommerceProductService.jmyService.jmyPerfil.perfil.eid+'/productos/'+this.product.id,
                        ruta:'fotos/',
                        permiso:'4', 
                    })
                    this._ecommerceProductService.jmyService.jmyFn.configuracionArchivos.next(archivosQuery);
                    
                    this._ecommerceProductService.jmyService.jmyFn.treeArchivos  .pipe(takeUntil(this._unsubscribeAll)).subscribe(respuesta=>{

                        console.log(respuesta);
                        let archivos:{tree:string[],url_iframe:object} = respuesta||{tree:[],url_iframe:{}};
                        
                        this.product.images = archivos.tree.map(archivo=>{
                            return {
                                default:(archivo.slice(0,-4)=='portada')?true:false,
                                id:archivo,
                                url:archivos.url_iframe['url_src']+archivo,
                                thumb:archivos.url_iframe['url_thumb']+archivo,
                                type:'image',
                            }
                        })
                        
                    });  
                }
                else
                {
                    this.pageType = 'new';
                    this.product = new Product();
                }

                this.productForm = this.createProductForm();
            });
    }

    /**
     * On destroy
     */
    ngOnDestroy(): void
    {
        // Unsubscribe from all subscriptions
        //this._ecommerceProductService.jmyService.jmyFn.configuracionArchivos.complete();
        //this._ecommerceProductService.jmyService.jmyFn.botonEnEsperaArchivos.complete();
        //  this._ecommerceProductService.jmyService.jmyFn.treeArchivos.complete();

        this._unsubscribeAll.next();
        this._unsubscribeAll.complete();
    }

    // -----------------------------------------------------------------------------------------------------
    // @ Public methods
    // -----------------------------------------------------------------------------------------------------

    /**
     * Create product form
     *
     * @returns {FormGroup}
     */
    createProductForm(): FormGroup
    {
        console.log(this.product);
        
        return this._formBuilder.group({
            id              : [this.product.id],
            name            : [this.product.name,[Validators.required]],
            handle          : [this.product.handle],
            description     : [this.product.description,[Validators.required]],
            categories      : [this.product.categories],
            tags            : [this.product.tags],
            images          : [this.product.images],
            priceTaxExcl    : [this.product.priceTaxExcl],
            priceTaxIncl    : [this.product.priceTaxIncl],
            taxRate         : [this.product.taxRate],
            comparedPrice   : [this.product.comparedPrice],
            quantity        : [this.product.quantity],
            sku             : [this.product.sku,[Validators.required]],
            width           : [this.product.width],
            height          : [this.product.height],
            depth           : [this.product.depth],
            weight          : [this.product.weight],
            extraShippingFee: [this.product.extraShippingFee],
            active          : [this.product.active],
            web             : [this.product.web]
        });
    }

    /**
     * Save product
     */
    saveProduct(product:Product=null): void
    {
        const data:Product = (product!=undefined)?product:this.productForm.getRawValue();
        data.handle = FuseUtils.handleize(data.name);
        data.images = this.product.images;
        this._ecommerceProductService.saveProduct(data)
            .then(() => {

                // Trigger the subscription with new data
                this._ecommerceProductService.onProductChanged.next(data);

                // Show the success message
                this._matSnackBar.open('Producto guardado', 'OK', {
                    verticalPosition: 'top',
                    duration        : 2000
                });
            });
    }

    /**
     * Add product
     */
    addProduct(): void
    {
        const data = this.productForm.getRawValue();
        data.handle = FuseUtils.handleize(data.name);

        this._ecommerceProductService.addProduct(data)
            .then(() => {

                // Trigger the subscription with new data
                this._ecommerceProductService.onProductChanged.next(data);

                // Show the success message
                this._matSnackBar.open('Producto aadido', 'OK', {
                    verticalPosition: 'top',
                    duration        : 2000
                });

                // Change the location with new one
                this._location.go('almacen/productos/' + this.product.id + '/' + this.product.handle);
            });
    }
}
