import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, Resolve, RouterStateSnapshot } from '@angular/router';
import { HttpClient } from '@angular/common/http';
import { BehaviorSubject, Observable } from 'rxjs';
import { JmyService } from 'app/concomsis/jmy/jmy.service';
import { jmyConfig } from 'environments/environment';

@Injectable()
export class AlmacenProductosService implements Resolve<any>
{
    products: any[];
    onProductsChanged: BehaviorSubject<any>;

    tienda: string;
    onTiendaChanged: BehaviorSubject<string>;

    /**
     * Constructor
     *
     * @param {HttpClient} _httpClient
     */
    constructor(
        private _httpClient: HttpClient,
        private jmyService: JmyService
    )
    {
        // Set the defaults
        this.onProductsChanged = new BehaviorSubject({});

        this.tienda='matriz';
        this.onTiendaChanged = new BehaviorSubject( this.tienda );

    }

    /**
     * Resolver
     *
     * @param {ActivatedRouteSnapshot} route
     * @param {RouterStateSnapshot} state
     * @returns {Observable<any> | Promise<any> | any}
     */
    resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<any> | Promise<any> | any
    {
        return new Promise((resolve, reject) => {

            Promise.all([
                this.getProducts()
            ]).then(
                () => {
                    resolve();
                },
                reject
            );
        });
    }

    /**
     * Get products
     *
     * @returns {Promise<any>}
     */
    getProducts(): Promise<any>
    {
        return new Promise((resolve, reject) => {
           this.jmyService.dbRealTime.list('almacen/'+jmyConfig.app.eid+'/productos/').valueChanges().subscribe(respuesta=>{
                this.products = respuesta || [];
                this.onProductsChanged.next(this.products);
                resolve(respuesta);
            })
    /*       
            this._httpClient.get('api/e-commerce-products')
                .subscribe((response: any) => {
                    this.products = response;
                    this.onProductsChanged.next(this.products);
                    resolve(response);
                }, reject);*/
        });
    
    }
}
