import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { ActivatedRouteSnapshot, Resolve, RouterStateSnapshot } from '@angular/router';
import { BehaviorSubject, Observable } from 'rxjs';
import { JmyService } from 'app/concomsis/jmy/jmy.service';
import { jmyConfig } from 'environments/environment';

import { Product } from 'app/main/apps/almacen/producto/producto.model';
@Injectable()
export class AlmacenProductoService implements Resolve<any>
{
    routeParams: any;
    product: any;
    onProductChanged: BehaviorSubject<any>;

    /**
     * Constructor
     *
     * @param {HttpClient} _httpClient
     */
    constructor(
        private _httpClient: HttpClient,
        public jmyService: JmyService
    )
    {
        // Set the defaults
        this.onProductChanged = new BehaviorSubject({});
    }

    /**
     * Resolver
     *
     * @param {ActivatedRouteSnapshot} route
     * @param {RouterStateSnapshot} state
     * @returns {Observable<any> | Promise<any> | any}
     */
    resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<any> | Promise<any> | any
    {
        this.routeParams = route.params;

        return new Promise((resolve, reject) => {

            Promise.all([
                this.getProduct()
            ]).then(
                () => {
                    resolve();
                },
                reject
            );
        });
    }

    /**
     * Get product
     *
     * @returns {Promise<any>}
     */
    getProduct(): Promise<any>
    {
        return new Promise((resolve, reject) => {
            if ( this.routeParams.id === 'new' )
            {
                this.onProductChanged.next(false);
                resolve(false);
            }
            else
            {
                this.jmyService.dbRealTime.object('almacen/'+jmyConfig.app.eid+'/productos/'+ this.routeParams.id).valueChanges().subscribe(resultado=>{
                    this.product = resultado;
                    this.onProductChanged.next(this.product);
                    resolve(resultado);
                })
                /*this._httpClient.get('api/e-commerce-products/' + this.routeParams.id)
                    .subscribe((response: any) => {
                        this.product = response;
                        this.onProductChanged.next(this.product);
                        resolve(response);
                    }, reject);*/
            }
        });
    }

    /**
     * Save product
     *
     * @param product
     * @returns {Promise<any>}
     */
    saveProduct(product:Product): Promise<any>
    {
        return new Promise((resolve, reject) => {
            this.jmyService.dbRealTime.object('almacen/'+jmyConfig.app.eid+'/productos/'+ product.id).set(product).then(resultado=>{
                resolve(resultado)
            }).catch(resultado=>{
                console.error(resultado);
                reject(resultado)
            })
            /*this._httpClient.post('api/e-commerce-products/' + product.id, product)
                .subscribe((response: any) => {
                    resolve(response);
                }, reject);*/
        });
    }

    /**
     * Add product
     *
     * @param product
     * @returns {Promise<any>}
     */
    addProduct(product): Promise<any>
    {
        console.log(product);
        return new Promise((resolve, reject) => {
            this.jmyService.dbRealTime.object('almacen/'+jmyConfig.app.eid+'/productos/'+ product.id).set(product).then(resultado=>{
                resolve(resultado)
            }).catch(resultado=>{
                console.error(resultado);
                reject(resultado)
            })
            /*this._httpClient.post('api/e-commerce-products/', product)
                .subscribe((response: any) => {
                    resolve(response);
                }, reject);*/
        });
    }
}
